@charset "UTF-8";
/**
 * arches - Global style system focused on utility classes
 * @version v1.1.0
 * @link https://github.com/ACC-Style/Arches/
 */
/**  Pre Header - modular scale default break points utility    *********************************/
/**  Version: 1.1.0    *********************************/
* {
  margin: 0;
  padding: 0;
}

img {
  display: block;
}

.clearfix {
  zoom: 1;
  /* For IE 6/7 (trigger hasLayout) */
}
.clearfix:before {
  content: "";
  display: table;
}
.clearfix:after {
  content: "";
  display: table;
  clear: both;
}

/* Smartphone resets and enhancements */
html {
  -webkit-font-smoothing: antialiased;
  /*-webkit-backface-visibility: hidden; /* this breaks the sticky widget on article and landing pages */
  /*-webkit-perspective: 1000; */
}

footer {
  /* enables on GPU processing, per http://davidwalsh.name/translate3d */
  transform: translate3d(0, 0, 0);
  -webkit-transform: translate3d(0, 0, 0);
}

a:link, a:hover, a:active {
  -webkit-tap-highlight-color: rbga(0, 0, 0, 0);
  outline: none;
  /* prevents pink highlight color on tap */
}

span {
  -webkit-transform: translateZ(0);
  /* enables on GPU processing */
}

